/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  --first-color: #0497E1;
  --first-color-alt: #008ad0;
  --title-color: #393939;
  --text-color: #707070;
  --text-color-light: #A6A6A6;
  --body-color: #FBFEFD;
  --container-color: #FFFFFF;

  /*========== Font and typography ==========*/
  --body-font: 'Poppins', sans-serif;
  --biggest-font-size: 2.25rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes ==========*/
  --mb-1: .5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}

@media screen and (min-width: 768px){
  :root{
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*========== BASE ==========*/
*,::before,::after{
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

html{
  scroll-behavior: smooth;
}

/*========== Variables Dark theme ==========*/
body.dark-theme{
  --title-color: #F1F3F2;
  --text-color: #C7D1CC;
  --body-color: #1D2521;
  --container-color: #27302C;
}

/*========== Button Dark/Light ==========*/
.change-theme{
  position: absolute;
  right: 1rem;
  top: 1.8rem;
  color: #707070;
  color: var(--text-color);
  font-size: 1rem;
  cursor: pointer;
}

body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  background-color: #FBFEFD;
  background-color: var(--body-color);
  color: #707070;
  color: var(--text-color);
  line-height: 1.6;
}

h1,h2,h3,p,ul{
  margin: 0;
}

ul{
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
}

img{
  max-width: 100%;
  height: auto;
}

/*========== CLASS CSS ==========*/
.section{
  padding: 4rem 0 2rem;
}

.section-title, .section-subtitle{
  text-align: center;
}

.section-title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.section-subtitle{
  display: block;
  color: #0497E1;
  color: var(--first-color);
  font-weight: 500;
  font-weight: var(--font-medium);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

/*========== LAYOUT ==========*/
.bd-container{
  max-width: 960px;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.bd-grid{
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #FBFEFD;
  background-color: var(--body-color);
}

/*========== HOME ==========*/
.home__container{
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  align-content: center;
}

.home__title{
  font-size: 2.25rem;
  font-size: var(--biggest-font-size);
  color: #0497E1;
  color: var(--first-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.home__subtitle{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.home__img{
  width: 300px;
  justify-self: center;
}

/*========== BUTTONS ==========*/
.button{
  display: inline-block;
  background-color: #0497E1;
  background-color: var(--first-color);
  color: #FFF;
  padding: .75rem 1rem;
  border-radius: .5rem;
  transition: .3s;
}

.button:hover{
  background-color: #008ad0;
  background-color: var(--first-color-alt);
}

/*========== ABOUT ==========*/
.about__data{
  text-align: center;
}

.about__description{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.about__img{
  width: 280px;
  border-radius: .5rem;
  justify-self: center;
}

/*========== SERVICES ==========*/
.services__container{
  grid-row-gap: 2.5rem;
  row-gap: 2.5rem;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.services__content{
  text-align: center;
}

.services__img{
  width: 64px;
  height: 64px;
  fill: #0497E1;
  fill: var(--first-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.services__title{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.services__description{
  padding: 0 1.5rem;
}

/*========== MENU ==========*/
.menu__container{
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
}
.menu__content{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0497E1;
  background-color: var(--first-color);
  border-radius: .5rem;
  box-shadow: 0 2px 4px rgba(3,74,40,.15);
  padding: .75rem;
  height: 300px;
}
.menu-button-input:hover{
  fill: white;
}
.menu__img{
  width: 100px;
  align-self: center;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.menu__name, .menu__preci{
  font-weight: 600;
  font-weight: var(--font-semi-bold);
  color: #393939;
  color: var(--title-color);
}

.menu__name{
  font-size: .938rem;
  font-size: var(--normal-font-size);
  color: #FFFFFF;
  color: var(--container-color);
}

.menu__detail, .menu__preci{
  font-size: .813rem;
  font-size: var(--small-font-size);
}

.menu__detail{
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.menu__button{
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  background-color: #FFFFFF;
  background-color: var(--container-color);
  padding: .625rem .813rem;
  border-radius: .5rem 0 .5rem 0;
}

/*========== APP ==========*/
.app__data{
  text-align: center;
}

.app__description{
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
}

.app__stores{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.app__store{
  width: 120px;
  margin: 0 .5rem;
  margin: 0 var(--mb-1);
}

.app__img{
  width: 230px;
  justify-self: center;
}

/*========== CONTACT ==========*/
.contact__container{
  text-align: center;
}

.contact__description{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

/*========== FOOTER ==========*/
.footer__container{
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.footer__logo{
  font-size: 1rem;
  font-size: var(--h3-font-size);
  color: #0497E1;
  color: var(--first-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

.footer__description{
  display: block;
  font-size: .813rem;
  font-size: var(--small-font-size);
  margin: .25rem 0 1.5rem;
  margin: .25rem 0 var(--mb-3);
}

.footer__social{
  font-size: 1.5rem;
  color: #393939;
  color: var(--title-color);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.footer__title{
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.footer__link{
  display: inline-block;
  color: #707070;
  color: var(--text-color);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.footer__link:hover{
  color: #0497E1;
  color: var(--first-color);
}

.footer__copy{
  text-align: center;
  font-size: .813rem;
  font-size: var(--small-font-size);
  color: #A6A6A6;
  color: var(--text-color-light);
  margin-top: 3.5rem;
}

/*========== MEDIA QUERIES ==========*/
@media screen and (min-width: 576px){
  .home__container,
  .about__container,
  .app__container{
    grid-template-columns: repeat(2,1fr);
    align-items: center;
  }

  .about__data, .about__initial,
  .app__data, .app__initial,
  .contact__container, .contact__initial{
    text-align: left;
    text-align: initial;
  }

  .about__img, .app__img{
    width: 380px;
    order: -1;
  }

  .contact__container{
    grid-template-columns: 1.75fr 1fr;
    align-items: center;
  }
  .contact__button{
    justify-self: center;
  }
}

@media screen and (min-width: 768px){
  body{
    margin: 0;
  }

  .section{
    padding-top: 8rem;
  }
  .change-theme{
    position: static;
    position: initial;
    margin-left: 1rem;
    margin-left: var(--mb-2);
  }

  .home__container{
    height: 100vh;
    justify-items: center;
  }

  .services__container,
  .menu__container{
    margin-top: 3rem;
    margin-top: var(--mb-6);
  }

  .menu__container{
    grid-template-columns: repeat(3, 210px);
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
  }
  .menu__content{
    padding: 1.5rem;
  }
  .menu__img{
    width: 130px;
  }

  .app__store{
    margin: 0 .5rem 0 0;
    margin: 0 var(--mb-1) 0 0;
  }
}

@media screen and (min-width: 960px){
  .bd-container{
    margin-left: auto;
    margin-right: auto;
  }

  .home__img{
    width: 500px;
  }

  .about__container,
  .app__container{
    grid-column-gap: 7rem;
    -webkit-column-gap: 7rem;
            column-gap: 7rem;
  }
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px) {
    .home__container {
        height: 640px;
    }
}
